<template>
  <div class="header">
    <div class="search">
        <input
          type="text"
          class="search_content"
          placeholder="请输入关键词"
          v-model="keyword"
        />
        <button class="search_btn" @click="search">搜索</button>
        <ul class="search_res">
          <li
            v-for="(item, index) in matchList"
            :key="index"
            @click="
              $router.push({
                name: 'detail',
                params: { filename: item }
              })
            "
          >
            {{ item }}
          </li>
        </ul>
        <div v-if="isNoResult" class="noresult">抱歉，暂无搜索结果</div>
    </div>
  </div>
</template>
<script>
export default {
  name: "search",
  data() {
    return {
      keyword: "",
      newsList: [],
      matchList: [],
      isNoResult: false
    };
  },
  mounted: function() {
    var _this = this;
    const files = require.context("/public/newsdata", false, /.csv$/).keys();
    files.forEach(key => {
      _this.newsList.push(key.replace(/(\.\/|\.csv)/g, ""));
    });
  },
  methods: {
    search() {
      if (this.keyword) {
        this.isNoResult = false;
        this.matchList = this.newsList.filter(key =>
          key.trim().match(this.keyword)
        );
        if (this.matchList.length === 0) {
          this.isNoResult = true;
        }
      } else {
        alert("关键词不能为空！");
      }
    },
    select() {
      this.$router.push("/detail/:filename");
    }
  }
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
.search {
  position: absolute;
  top: 50px;
  left: 488px;
  width: 400px;
  height: 40px;
}
.search_content {
  width: 300px;
  height: 40px;
  padding-left: 10px;
  border: 2px solid rgb(199, 199, 199);
  border-right: none;
  font-size: 14px;
  vertical-align: top;
  box-sizing: border-box;
  border-radius: 6px 0 0 6px;
  outline: none;
}
.search_btn {
  width: 80px;
  height: 40px;
  border: 0;
  background-color: blue;
  color: #fff;
  font-size: 14px;
  line-height: 40px;
  border-radius: 0 6px 6px 0;
  cursor: pointer;
  outline: 0;
}
.search_btn:hover {
  background-color: rgb(47, 47, 250);
}
.search input:focus {
  border: 2px solid blue;
  border-right: none;
}
.search_res {
  width: 300px;
  position: absolute;
  top: 40px;
  left: 0;
  text-align: left;
  z-index: 1;
  background-color: #fff;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.08);
}
.search_res li {
  padding: 10px;
  cursor: pointer;
  list-style: none;
}
.search_res li:hover {
  color: blue;
}
.noresult {
  padding: 10px;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.08);
  width: 280px;
  position: absolute;
  top: 40px;
  left: 0;
}
</style>