<template>
  <div class="cloud">
    <svg :width="width" :height="height" @mousemove="listener($event)">
      <a
        v-for="(tag, index) in tags"
        :key="index"
        @click="
          $router.push({
            name: 'detail',
            params: { filename: tag.text }
          })
        "
      >
        <text
          :x="tag.x"
          :y="tag.y"
          :font-size="16 * (600 / (600 - tag.z))"
          :fill-opacity="(400 + tag.z) / 600"
          :stroke="tag.color"
        >
          {{ tag.text }}
        </text>
      </a>
    </svg>
  </div>
</template>
<script>
export default {
  data() {
    return {
      width: 1200,
      height: 480,
      tagsNum: 20,
      RADIUS: 200,
      newsList: [], //标签列表
      speedX: Math.PI / 360,
      speedY: Math.PI / 360,
      tags: [],
      text: []
    };
  },
  methods: {
    getRandomNum() {
      return Math.floor(Math.random() * (255 + 1));
    },
    rotateX(angleX) {
      var cos = Math.cos(angleX);
      var sin = Math.sin(angleX);
      for (let tag of this.tags) {
        var y1 = (tag.y - this.CY) * cos - tag.z * sin + this.CY;
        var z1 = tag.z * cos + (tag.y - this.CY) * sin;
        tag.y = y1;
        tag.z = z1;
      }
    },
    rotateY(angleY) {
      var cos = Math.cos(angleY);
      var sin = Math.sin(angleY);
      for (let tag of this.tags) {
        var x1 = (tag.x - this.CX) * cos - tag.z * sin + this.CX;
        var z1 = tag.z * cos + (tag.x - this.CX) * sin;
        tag.x = x1;
        tag.z = z1;
      }
    },
    listener(event) {
      //响应鼠标移动
      var x = event.clientX - this.CX;
      var y = event.clientY - this.CY;
      this.speedX =
        x * 0.0001 > 0
          ? Math.min(this.RADIUS * 0.00002, x * 0.0001)
          : Math.max(-this.RADIUS * 0.00002, x * 0.0001);
      this.speedY =
        y * 0.0001 > 0
          ? Math.min(this.RADIUS * 0.00002, y * 0.0001)
          : Math.max(-this.RADIUS * 0.00002, y * 0.0001);
    }
  },
  computed: {
    CX() {
      return this.width / 2;
    },
    CY() {
      return this.height / 2;
    }
  },
  created() {
    //初始化标签位置
    this.newsList = ['山东舰', '056型护卫舰', '歼-20战斗机','“瓦良格”号航空母舰', 
                    '东风-31洲际导弹','F-22“猛禽”战斗机', 'B-2隐身轰炸机', 
                    '辽宁号', '“阿利-伯克”级导弹驱逐舰','T-50战斗机',
                    '白杨-M洲际导弹', '黑鹰主战坦克', '“基洛”级潜艇', '“北风之神”级战略核潜艇',
                    '054型护卫舰', '99式主战坦克','V-22鱼鹰式倾转旋翼机', 'AH-64“阿帕奇”武装直升机',
                    '“库兹涅佐夫”号航母', '苏-27战斗机']
    let tags = [];
    for (let i = 0; i < this.tagsNum; i++) {
      let tag = {};
      let k = -1 + (2 * (i + 1) - 1) / this.tagsNum;
      let a = Math.acos(k);
      let b = a * Math.sqrt(this.tagsNum * Math.PI); //计算标签相对于球心的角度
      /* 标签文本 */
      tag.text = this.newsList[i];
      tag.color = "rgb(" + this.getRandomNum() + "," + this.getRandomNum() + "," + this.getRandomNum() + ")";
      tag.x = this.CX + this.RADIUS * Math.sin(a) * Math.cos(b); //根据标签角度求出标签的x,y,z坐标
      tag.y = this.CY + this.RADIUS * Math.sin(a) * Math.sin(b);
      tag.z = this.RADIUS * Math.cos(a);
      tag.href = ""; //给标签添加链接
      tags.push(tag);
    }
    this.tags = tags; //让vue替我们完成视图更新
  },
  mounted() {
    //使球开始旋转
    setInterval(() => {
      this.rotateX(this.speedX);
      this.rotateY(this.speedY);
    }, 17);
  }
};
</script>
<style>
.cloud {
  margin-top: 100px;
}
.cloud svg a {
  cursor: pointer;
}
</style>